import { useFormik } from 'formik';
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import {AddFoodMenu} from "../store/action"



export default function AddMenu() {

    let dispatch = useDispatch();
    
    const [menu_name, setMenuName] = useState("")
    const [amount, setAmount] = useState("")
    const [menu_picture, setMenu_picture] = useState(null)

    const handleImageChange=(e)=>{
      setMenu_picture(e.target.files[0])
    }

    const handleMenuNameChange=(e)=>{
      setMenuName(e.target.value)
   }

   const handleAmountChange=(e)=>{
    setAmount(e.target.value)
  }

  const SendMenu=(e)=>{
       e.preventDefault();
       alert("helllllllllllllloooooooooooooooooooo")
    
      const newUser = new FormData();
      newUser.append('menu_name', menu_name)
      newUser.append('amount', amount)
      newUser.append('menu_image_url', menu_picture)

     
       dispatch(AddFoodMenu(newUser));

   }

    return (
        <div className="flex w-full">
        <div className="bg-gray-200 text-white w-full font-bold rounded-lg border shadow-lg">
          <form onSubmit={SendMenu} className="w-1/2 m-auto mt-4 space-y-2">
                    <div className="flex flex-col space-y-2">
                        <input type="text" className="p-3 w-full bg-transparent text-black outline-black" placeholder="Name"
                            value={menu_name}
                            onChange={handleMenuNameChange}/>
                        <input type="text"  className="p-3 w-full bg-transparent text-black outline-black" placeholder="Amount"
                                value={amount}
                                onChange={handleAmountChange} />
                        <input type="file" className="p-3 w-full bg-transparent text-black outline-black" 
                            onChange={handleImageChange} 
                            />
                        
                        <button type="submit" className="bg-gray-400 p-2">
                            Save
                        </button>
                    </div>
                </form>
        </div>
      </div>
    )
}
