import React from 'react'

export default function StartsItem({icon ,label,number, backColor , iconBackColor}) {
    return (
        <div className={`w-full ${backColor} flex space-x-3 p-4 m-4`}>
                {/* <div className={`w-full mb-8 rounded  p-4 ${backColor} max-h-24 md:flex space-x-8 w-1/3`}> */}
            <span className={`${iconBackColor} text-white rounded-full  flex items-center justify-center md:h-16 w-16`}>{icon}</span>
            <div className="flex flex-col ">
                <span className="text-white text-3xl semibold">{label}</span>
                <span className="text-white font-medium">{number}</span>
            </div>
        </div>
    )
}


