import axios from "axios";
import jwt from 'jsonwebtoken';
import { history } from '../Util/cust';
import {BaseUrl} from '../Util/constants'

export const AttemptLogin = (user) => async (dispatch) => {

  console.log("user in action",user);
    
     await axios.post(`${BaseUrl}user/login`,user)
      .then(res =>{
        localStorage.setItem('token', res.data.accessToken);
        dispatch(setCurrentUser(jwt.decode(res.data.accessToken)))
        window.location="/"
      }
      ).catch(err=>{
        console.log("login error",err)
      }); 
  };

  export const setCurrentUser=(user)=>{
    return {
      type:"SET_CURRENT_USER",
      payload:user
    }
  }


 export const GetAllUsers = () => async (dispatch) => {
       
  await axios.get(`${BaseUrl}user/getAllEmployeeUsers`)
   .then(res =>{
     console.log("all users",res.data);
     //dispatch all usersList to store
     dispatch({
       type:"SET_USERS",
       payload:res.data
     });
   }
   ).catch(err=>{
     console.log("FETCHING USERS ERROR",err)
   }); 
};

export const RegisterEmployee = (user) => async (dispatch) => {
  

  await axios.post(`${BaseUrl}user/register`,user)
   .then(res =>{
     dispatch({
       type:"SET_EMPLOYEE",
       payload:res.data
     });
     alert("User created Succesfully");
    history.push('/users')
   }
   ).catch(err=>{
     console.log("EMP ERROR",err.response)
   }); 
};


export const AddFoodMenu = (menu) => async (dispatch) => {
  
  

  await axios.post(`${BaseUrl}menu/addMenu`,menu)
   .then(res =>{
     console.log("all menu",res.data);
     dispatch({
       type:"SET_MENU",
       payload:res.data
     });
     //window.location = "/menuList";
     window.location.reload();
    }
   ).catch(err=>{
     console.log("EMP ERROR",err.response)
   }); 
};


