import { useFormik } from 'formik';
import React from 'react';
import { useDispatch } from 'react-redux';
import {RegisterEmployee} from "../store/action"



export default function RegisterUser() {

    let dispatch = useDispatch();

    const validate = (values)=>{
        const errors = {};
        
          if (!values.email) {
            errors.email = 'Required';
          } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'Invalid email address';
          }

          if (!values.password) {
            errors.password = 'Required';
          } else if (values.password.length > 15) {
            errors.password = 'Must be 15 characters or less';
          }

          if (!values.firstname) {
            errors.firstname = 'Required';
          } else if (values.firstname.length > 15) {
            errors.firstname = 'Must be 15 characters or less';
          }

          if (!values.lastname) {
            errors.lastname = 'Required';
          } else if (values.lastname.length > 15) {
            errors.lastname = 'Must be 15 characters or less';
          }

        return errors;
    }

    const formik = useFormik({
         initialValues : {
            email:"",
            password:"",
            firstname:"",
            lastname:"",
            phone_number:""
        },
        validate,
        onSubmit:values=>{
            console.log("data",values)
            dispatch(RegisterEmployee(values));

        }
    });

    return (
        <div className="flex w-full">
        <div className="bg-gray-200 text-white w-full font-bold rounded-lg border shadow-lg">
          <form onSubmit={formik.handleSubmit} className="w-1/2 m-auto mt-4 space-y-2">
                    <div className="flex flex-col space-y-2">
                        <input type="text" className="p-3 w-full bg-transparent text-black outline-black" placeholder="Firstname"
                            name="firstname"
                            onChange={formik.handleChange} 
                            value={formik.values.firstname}/>
                        {formik.errors.firstname ? <div className="text-red-600">{`*${formik.errors.firstname}`}</div>:null}
                        <input type="text"  className="p-3 w-full bg-transparent text-black outline-black" placeholder="Lastname"
                                name="lastname" 
                                onChange={formik.handleChange} 
                                value={formik.values.lastname} />
                        {formik.errors.lastname ? <div className="text-red-900">{`*${formik.errors.lastname}`}</div>:null}
                        <input type="text" className="p-3 w-full bg-transparent text-black outline-black" placeholder="Email"
                            name="email"
                            onChange={formik.handleChange} 
                            value={formik.values.email}/>
                        {formik.errors.email ? <div className="text-red-600">{`*${formik.errors.email}`}</div>:null}
                        <input type="password"  className="p-3 w-full bg-transparent text-black outline-black" placeholder="Password"
                                name="password" 
                                onChange={formik.handleChange} 
                                value={formik.values.password} />
                        {formik.errors.password ? <div className="text-red-900">{`*${formik.errors.password}`}</div>:null}
                        <input type="text"  className="p-3 w-full bg-transparent text-black outline-none" placeholder="Phone"
                                name="phone_number" 
                                onChange={formik.handleChange} 
                                value={formik.values.phone_number} />
                        {formik.errors.phone_number ? <div className="text-red-900">{`*${formik.errors.phone_number}`}</div>:null}
                        <button type="submit" className="bg-gray-400 p-2">
                            Login
                        </button>
                    </div>
                </form>
        </div>
      </div>
    )
}
