
const initialState = {
    isAuthenticated:false,
    user:{},
    usersList:[],
    menuList:[],
    customerList:[],
    orderList:[]
}


export default function authReducer(state=initialState,action){
    switch(action.type){
        case "SET_CURRENT_USER":
        return {
            isAuthenticated: true,
            user:action.payload
        }
        case "LOGOUT":
            return {
                isAuthenticated: false,
                user:{}
            }
            case "SET_USERS":
                return {
                    ...state,
                    usersList:action.payload
                }
            case "SET_EMPLOYEE":
                return {
                    ...state,
                     usersList:action.payload
                }
                case "SET_MENU":
                return {
                    ...state,
                     menuList:action.payload
                }
                case "GET_MENU":
                return {
                    ...state,
                     menuList:action.payload
                }
                case "SET_CUSTOMER":
                return {
                    ...state,
                    customerList:action.payload
                }
                case "SET_ORDERS":
                return {
                    ...state,
                    orderList:action.payload
                }
        default:
        return state;
    }
}

