import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import {AttemptLogin} from "../../store/action"
import '../../index.css';
import { useFormik } from 'formik';




export default function Login() {

    let dispatch = useDispatch();

    const validate = (values)=>{
        const errors = {};
        
          if (!values.email) {
            errors.email = 'Required';
          } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'Invalid email address';
          }

          if (!values.password) {
            errors.password = 'Required';
          } else if (values.password.length > 15) {
            errors.password = 'Must be 15 characters or less';
          }

        return errors;
    }

    const formik = useFormik({
         initialValues : {
            email:"",
            password:""
        },
        validate,
        onSubmit:values=>{
            console.log("data",values)
            dispatch(AttemptLogin(values));

        }
    });

    return (
        <div className="flex flex-col items-center justify-center h-screen">
    
        <div className="bg-gray-200 text-white font-bold rounded-lg border shadow-lg p-10">
        <form onSubmit={formik.handleSubmit}>
                    <div className="flex flex-col space-y-2">
                        <input type="text" className="p-3 w-full bg-white text-black border"  
                            name="email"
                            onChange={formik.handleChange} 
                            value={formik.values.email}/>
                        {formik.errors.email ? <div className="text-red-600">{`*${formik.errors.email}`}</div>:null}
                        <input type="password"  className="p-3 w-full bg-white text-black border" 
                                name="password" 
                                onChange={formik.handleChange} 
                                value={formik.values.password} />
                        {formik.errors.password ? <div className="text-red-900">{`*${formik.errors.password}`}</div>:null}
                        <button type="submit" className="bg-gray-400 p-2">
                            Login
                        </button>
                    </div>
                </form>
        </div>
      </div>
        
    )
}
