import React from 'react'

export default function SingleMenu({title,amount,foodImage}) {
    return (
        <div className="bg-gray-300 w-full md:w-1/4">
          <img style={{width:'277px', height:'277px'}} src={foodImage}/>
        {/* <img style={{width:'277', height:'277'}} src="https://tmbidigitalassetsazure.blob.core.windows.net/rms3-prod/attachments/37/1200x1200/Pizza-from-Scratch_EXPS_FT20_8621_F_0505_1_home.jpg"/> */}
                      <div className="flex flex-col p-2">
                        <span className="font-bold font-serif ">{title}</span>
                        <span className="font-medium font-sans">{`GHC ${amount}`}</span>
                      </div>
        </div>
    )
}
