import React, { useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { GetAllMenu } from '../store/action';


import { useHistory } from 'react-router';
import SingleMenu from '../components/SingleMenu';



export default function FoodMenuList() {

    let dispatch = useDispatch();
    let history = useHistory();
    
    useEffect(() => {
        dispatch(GetAllMenu());
    }, [])

    const {menuList} = useSelector(state => state.auth);
    console.log("menu list from store", menuList);


    return (
        <div className="w-full">
            <div className="flex flex-col m-8 space-y-2">
              <div className="flex justify-end">
                <button className="bg-gray-300 p-2 rounded" onClick={()=>history.push("/addMenu")}>Add Menu</button>
              </div>

              <div className="flex space-x-2">
                {menuList?(menuList.data.map(menu=>(
                   <SingleMenu title={menu.menu_name} amount={menu.amount} foodImage={menu.menu_image_url}/>
                 ))):(<p>No data to show</p>)}
                </div>
            </div>
        </div>
    )
}
